<template>
    <div id="menuBox">
        <!-- 顶部 -->
        <div class="menuBox">
            <div class="logo">
                <img :src="logoPng"/>
                <img class="text" :src="fontPng"/>
            </div>
            <div class="userInfo">
                <el-tooltip effect="dark" :content="userInfo?.username ? userInfo?.username : '未知用户'" placement="bottom">
                    <div class="name">{{ userInfo?.username ? $root.shortStr(userInfo.username,5) : '未知用户' }}</div>
                </el-tooltip>
                
                <el-tooltip class="exitButton" effect="dark" content="退出" placement="bottom">
                    <div class="exit" @click="$root.logout()"></div>
                </el-tooltip>
            </div>
        </div>
        <!-- 底部 -->
        <div class="bottomBox" @mouseleave="hideChildList()">
            <ul>
                <li v-for="item in menus" :key="item.name" @click="linkTo(item)" :class="item.isActive?'active':''">
                    <i :class="'icon '+item.icon"></i>
                    <span>{{ item.name }}</span>
                </li>
            </ul>
            <!-- 子菜单 -->
             <div class="childContent" :style="childStyle">
                <ul>
                    <li v-for="item in childContent" :key="item.name" @click="linkTo(item)">
                        <div class="item">
                            <div class="name">
                                <i :class="'icon '+item.icon"></i>
                                <span>{{ item.name }}</span>
                            </div>
                        </div>
                    </li>
                </ul>
             </div>
        </div>
    </div>
</template>

<script>
import fontPng from "@/assets/img/font.png";
import logoPng from "@/assets/img/logo.png";

export default {
    name: 'menuBox',
    props: {
        userInfo:{
            type:Object,
            default:function(){
                return null
            }
        }
    },
    data() {
        return {
            fontPng:fontPng,
            logoPng:logoPng,
            childContent:[],
            childStyle:"",
            ceilNum: 3,
            rowHeight: 62,
            menus:[
                {
                    "name":"首页",
                    "url":"/usercenter",
                    "isParent":false,
                    "icon":"el-icon-s-home",
                    "isActive":false,
                },
                {
                    "name":"服务",
                    "isParent":true,
                    "icon":"el-icon-s-help",
                    "isActive":false,
                    "child":[
                        {
                            "name":"服务器状态",
                            "url":"/servers",
                            "icon":"el-icon-s-unfold",
                            "content":"在这里您可以查看所有服务器的状态与负载情况"
                        },
                        {
                            "name":"隧道列表",
                            "url":"/tunnel",
                            "icon":"el-icon-s-unfold",
                            "content":"在这里您可以添加、修改、删除、查看您所拥有的隧道列表"
                        },
                        {
                            "name":"软件下载",
                            "url":"/download",
                            "icon":"el-icon-download",
                            "content":"在这里您可以下载软件，并查看软件更新日志"
                        },
                        {
                            "name":"备案审核",
                            "url":"/domainauth",
                            "icon":"el-icon-s-claim",
                            "content":"在这里您可以对您的域名申请备案"
                        },
                        {
                            "name":"工单系统",
                            "url":"/ticket",
                            "icon":"el-icon-s-order",
                            "content":"在这里您可以向客服人员提交工单、查看或回复工单"
                        }
                    ]
                },
                {
                    "name":"用户",
                    "url":"/user",
                    "icon":"el-icon-user-solid",
                    "isParent":false,
                    "isActive":false,
                },
                {
                    "name":"商城",
                    "url":"/mall",
                    "icon":"el-icon-s-shop",
                    "isParent":false,
                    "isActive":false,
                },
                {
                    "name":"帮助",
                    "url":"/help",
                    "icon":"el-icon-question",
                    "isParent":false,
                    "isActive":false,
                },
            ],
        }
    },
    watch: { 
        //深度监听
        $route: {
            handler() {
                this.menuActive(this.$route.path);
            },
            deep: true,
            immediate: true
        },
    },
    methods: {
        //计算菜单激活项
        menuActive(url=""){ 
            if(this.menus.length === 0){
                return;
            }
            this.menus.forEach(mainItem => {
                // 默认不激活
                mainItem.isActive = false;

                // 直接匹配主菜单
                if(mainItem.url === url){
                    mainItem.isActive = true;
                    return;
                }

                // 匹配子菜单
                if(mainItem.child){
                    mainItem.isActive = mainItem.child.some(childItem => childItem.url === url);
                }
            });
        },
        //计算子菜单样式
        computeChildStyle(){
            let height = Math.ceil(this.childContent.length/this.ceilNum)*this.rowHeight;
            this.childStyle = "height:"+(height+50)+"px;";
            setTimeout(() => {
                this.childStyle = this.childStyle + "overflow:auto;";
            }, 301);
        },
        linkTo(item){
            if(!item?.url||item?.isParent){
                this.openChildList(item);
                return;
            }
            let url = item?.url;
            this.hideChildList();
            this.$root.linkTo(url)
        },
        //打开子菜单
        openChildList(item){
            if(!item?.isParent)return;
            this.$root.enableMask(true,"61px",6);
            this.childContent = item?.child ?? [];
            this.computeChildStyle();
        },
        //关闭子菜单
        hideChildList(){
            this.childStyle = "";
            this.$root.enableMask(false);
            setTimeout(() => {
                this.childContent = [];
            }, 301);
        },
    },
    mounted() {
        
    }
}
</script>

<style lang="scss" scoped src="@/assets/css/lib/menuBox.scss"></style>