<template>
    <div id="userPage" class="page">
        <el-tabs v-model="activeName">
            <el-tab-pane label="卡密激活" name="usecard">
                <useCard @finish="reloadData"/>
            </el-tab-pane>

            <el-tab-pane label="卡密列表" name="cardlist">
                <!-- 卡密列表S -->
                <div class="cardTable card">
                <div class="cardTitle">卡密列表</div>
                <div class="toolsBar">
                    <div class="row">
                        <div class="title">搜索</div>
                    </div>
                    <div class="row">
                        <input type="text" placeholder="请输入卡密" v-model="searchCode" clearable/>
                    </div>
                    <div class="row">
                        <div class="btnGroup">
                            <buttonLib btnClass="btn" text="刷新" @click="reloadData()"/>
                            <buttonLib btnClass="btn" text="清空筛选" @click="searchCode=''"/>
                        </div>
                    </div>
                </div>
                <div :class="loading?'tableBox loadingLocal':'tableBox'">
                    <el-table :data="cardsData" border style="width: 100%" row-class-name="tableRow" @sort-change="sortChange">
                        <el-table-column type="expand" :resizable="false">
                            <template slot-scope="scope">
                                <div class="expand">
                                    <div class="item">
                                        <span class="lable">卡密名称:</span>
                                        <span class="value">{{scope.row.name}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">卡密:</span><br>
                                        <span class="value">{{scope.row.code}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">包含隧道(个):</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">包含流量(MB):</span>
                                        <span class="value">{{scope.row.traffic_amount}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">会员等级:</span>
                                        <span class="value">{{ scope.row?.level_text?scope.row.level_text:'无' }}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">有效期至:</span>
                                        <span class="value">{{$root.getDate(scope.row.exptime)}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">使用时间:</span>
                                        <span class="value">{{$root.getDate(scope.row.usetime)}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">包含隧道(个):</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">包含隧道(个):</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                    <div class="item">
                                        <span class="lable">包含隧道(个):</span>
                                        <span class="value">{{scope.row.tunnels_num}}</span>
                                    </div>
                                </div>                                 
                            </template>
                        </el-table-column>
                        <el-table-column prop="name" label="卡密名称" sortable="custom" :resizable="false">
                        </el-table-column>

                        <el-table-column prop="status" label="卡密状态" sortable="custom" :resizable="false">
                            <template slot-scope="scope">
                                <el-tag v-if="scope.row.status == 1" type="success">使用中</el-tag>
                                <el-tag v-if="scope.row.status == 2" type="danger">已到期</el-tag>
                                <el-tag v-if="scope.row.status == 3" type="info">已折算</el-tag>
                                <el-tag v-if="scope.row.traffic_status&&scope.row.traffic_status != ''" type="danger">流量已用尽</el-tag>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div class="pagenation">
                        <pageLib :total="totalPage" :page="currentPage" @change="changePage"/>
                    </div>
                </div>
                </div>
                <!-- 卡密列表E -->
            </el-tab-pane>

            <el-tab-pane label="用户信息" name="userdata">
                <userData :userInfo="userInfo"/>
            </el-tab-pane>

            <el-tab-pane label="用户配置" name="userconfig">
                <!-- 操作列表S -->
                <div class="controller card">
                <div class="cardTitle">操作</div>
                <ul>
                    <li>
                        <div class="controllerBtn" @click="isShowChangepwd=true">修改密码</div>
                    </li>
                    <li>
                        <div class="controllerBtn" @click="isShowChangeemail=true">修改邮箱</div>
                    </li>
                    <li>
                        <div class="line">
                            通知设置
                            <el-tooltip
                            class="help"
                            effect="dark"
                            content="打开相应通知开关,即可即时收到邮件通知提醒"
                            placement="top">
                                <i class="icon el-icon-question"></i>
                            </el-tooltip>                            
                        </div>
                    </li>
                    <li>
                        <div class="switch">
                            <el-switch
                                v-model="isOpenTrafficAlert"
                                active-color="#b81010"
                                inactive-color="#C0C4CC">
                            </el-switch>
                            <span>{{isOpenTrafficAlert?'流量预警已开启':'流量预警已关闭'}}</span>
                        </div>
                        <div class="inputBox" v-if="isOpenTrafficAlert">
                            <div class="pre">预警阈值</div>
                            <input class="hasAppend hasPre" type="number" v-model="notifyConfigForm.traffic" placeholder="请输入流量预警阈值(MB)"/>
                            <div class="append">MB</div>
                        </div>
                        <buttonLib v-if="isOpenTrafficAlert" class="saveBtn" size="small" text="保存" @click="saveNotifyConfig"/>
                    </li>
                    <li>
                        <div class="switch">
                            <el-switch
                                v-model="isOpenEXPAlert"
                                active-color="#b81010"
                                inactive-color="#C0C4CC">
                            </el-switch>
                            <span>{{isOpenEXPAlert?'卡密临期提醒已开启':'卡密临期提醒已关闭'}}</span>
                        </div>
                        <div class="inputBox" v-if="isOpenEXPAlert">
                            <div class="pre">临期天数</div>
                            <input class="hasAppend hasPre" type="number" v-model="notifyConfigForm.cardday" placeholder="请临期天数"/>
                            <div class="append">天</div>
                        </div>
                        <buttonLib v-if="isOpenEXPAlert" class="saveBtn" size="small" text="保存" @click="saveNotifyConfig"/>
                    </li>
                </ul>
                </div>
                <!-- 操作列表E -->

                <!-- 修改密码S -->
                <dialogLib ref="changepwd" v-if="isShowChangepwd" title="修改密码" maxWidth="400px" height="auto" @hide="isShowChangepwd=false">
                    <div class="changepwd">
                        <div class="item">
                            <el-alert
                                title="我们将向您绑定的邮箱发送一封包含验证码的邮件,请查收后填入下方的验证码输入框。"
                                type="info"
                                :closable="false"
                                effect="dark">
                            </el-alert>
                        </div>
                        <div class="item">
                            <div class="lable">验证码</div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changepwdForm.code" placeholder="请输入验证码"/>
                                </div>
                                <buttonLib btnClass="btn" text="获取验证码" @click="getpwdToken"/>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable">新密码</div>
                            <div class="row">                        
                                <div class="value">
                                    <input type="password" v-model="changepwdForm.newpwd" placeholder="请输入新密码"/>
                                </div>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable">确认密码</div>
                            <div class="row">                        
                                <div class="value">
                                    <input type="password" v-model="changepwdForm.confirmpwd" placeholder="请再次输入密码"/>
                                </div>
                            </div>
                        </div>
                        <div class="btnBox">
                            <buttonLib btnClass="btn" text="确认修改" @click="changepwd"/>
                        </div>
                    </div>
                </dialogLib>
                <!-- 修改密码E -->

                <!-- 修改邮箱S -->
                <dialogLib ref="changeemail" v-if="isShowChangeemail" title="修改邮箱" maxWidth="400px" height="auto" @hide="isShowChangeemail=false">
                    <div class="changeemail">
                        <div class="item">
                            <el-alert
                                title="我们将向您当前绑定的邮箱和新邮箱分别发送一封包含验证码的邮件,请查收后填入相应的验证码输入框。"
                                type="info"
                                :closable="false"
                                effect="dark">
                            </el-alert>
                        </div>
                        <div class="item">
                            <div class="lable">当前邮箱验证码</div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changeemailForm.code" placeholder="请输入当前邮箱收到的验证码"/>
                                </div>
                                <buttonLib btnClass="btn" text="获取验证码" @click="getemailToken('changeemailS1')"/>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable">新邮箱地址</div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changeemailForm.email" placeholder="请输入新邮箱地址"/>
                                </div>
                                <buttonLib btnClass="btn" text="获取验证码" @click="getemailToken('changeemailS2')"/>
                            </div>
                        </div>
                        <div class="item">
                            <div class="lable">新邮箱验证码</div>
                            <div class="row">
                                <div class="value">
                                    <input v-model="changeemailForm.newcode" placeholder="请输入新邮箱收到的验证码"/>
                                </div>
                            </div>
                        </div>
                        <div class="btnBox">
                            <buttonLib btnClass="btn" text="确认修改" @click="changeEmail()"/>
                        </div>
                    </div>
                </dialogLib>
                <!-- 修改邮箱E -->
            </el-tab-pane>
        </el-tabs>
        <!-- hcaptcha 验证S -->
        <dialogLib ref="hcaptcha" v-if="isShowHcaptcha" title="人机验证" :hideWithMask="false" @hide="closeHcaptcha" height="auto" max-width="400px" >
            <vue-hcaptcha @verify="hcapResult" :sitekey="hCaptchaKey" ></vue-hcaptcha>
        </dialogLib>
        <!-- hcaptcha 验证E -->
    </div>
</template>

<script>
import axios from 'axios';
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';

import userData from '@/components/lib/userData.vue';
import useCard from '@/components/lib/useCard.vue';
import pageLib from '@/components/lib/unit/pageLib.vue';
import buttonLib from '@/components/lib/unit/buttonLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';

export default {
    name: 'userPage',
    components: {
        userData,
        useCard,
        pageLib,
        buttonLib,
        dialogLib,
        VueHcaptcha
    },
    data() {
        return {
            activeName:"userdata",
            userInfo: null,
            cardsData:[],
            totalPage:1,
            currentPage:1,
            limit:10,
            loading:false,
            URL:window.config.apiUrl,
            axiosC: new AbortController(),    //请求列表 用于取消请求
            sort:"status",
            order:"asc",
            searchCode:"",
            isShowChangepwd:false,
            changepwdForm:{
                code:"",
                newpwd:"",
                confirmpwd:""
            },
            isShowChangeemail:false,
            changeemailForm:{
                code:"",
                newcode:"",
                email:"",
            },
            isOpenTrafficAlert:false,
            isOpenEXPAlert:false,
            isShowHcaptcha:false,
            hCaptchaType:"changepwd",
            hCaptchaKey : window.config.hCaptcha,
            notifyConfigForm:{
                traffic:200,    //MB
                cardday:3,      //天数
            },
        }
    },
    watch: {
        searchCode:"debouncedGetData",
        userInfo:{
            handler(val){
                if(val){
                    this.isOpenTrafficAlert = val?.traffic_alert > 0;
                    this.isOpenEXPAlert = val?.cards_alert > 0;
                    this.notifyConfigForm = {
                        traffic:val?.traffic_alert > 0 ? val?.traffic_alert/1024 : 0,
                        cardday:val?.cards_alert > 0 ? val?.cards_alert : 0
                    }
                }
                else{
                    this.isOpenTrafficAlert = false;
                    this.isOpenEXPAlert = false;
                    this.notifyConfigForm = {
                        traffic:0,
                        cardday:0
                    }
                }
            },
            deep: true,
            immediate: true
        },
        "isOpenTrafficAlert":function(val){ 
            if(!val){
                this.notifyConfigForm.traffic = 0;
                this.saveNotifyConfig();
            }
        },
        "isOpenEXPAlert":function(val){ 
            if(!val){
                this.notifyConfigForm.cardday = 0;
                this.saveNotifyConfig();
            }
        },
    },
    methods: {
        //保存预警配置
        async saveNotifyConfig(){
            let data = null;
            try{
                data = await axios.post(this.URL+"/user/changeNotice",
                    this.notifyConfigForm,
                    {
                        signal: this.axiosC.signal,
                        headers: { token: localStorage.getItem("token") ?? "" },
                    })
            }
            catch(err){
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                this.closeChangeEmail();
                return;
            }
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '提示',
                message: data?.data?.msg,
            });
        },
        //调用修改邮箱接口
        async changeEmail() { 
            //检查表单S
            if(this.changeemailForm.code=="" || this.changeemailForm.newcode=="" || this.changeemailForm.email==""){
                this.$notify.error({
                    title: '提示',
                    message:"请填写完整信息"
                });
                return;
            }
            //检查表单E
            this.$root.loading(true);
            let data = null;
            let postData = {
                code:this.changeemailForm.code,
                newcode:this.changeemailForm.newcode,
                email:this.changeemailForm.email
            }
            try{
                data = await axios.post(this.URL+"/user/changeemail",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                this.closeChangeEmail();
                return;
            }
            this.closeChangeEmail();
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '提示',
                message: data?.data?.msg,
            });
        },
        //关闭修改邮箱弹窗
        closeChangeEmail() {
            this.changeemailForm = {
                code:"",
                newcode:"",
                email:""
            };
            this.$refs?.changeemail?.hide();
        },
        //关闭修改密码弹窗
        closeChangePwd() {
            this.changepwdForm = {
                code:"",
                newpwd:"",
                confirmpwd:""
            };
            this.$refs?.changepwd?.hide();
        },
        //调用修改密码接口
        async changepwd() { 
            //检查表单S
            if(this.changepwdForm.code=="" || this.changepwdForm.newpwd=="" || this.changepwdForm.confirmpwd==""){
                this.$notify.error({
                    title: '提示',
                    message:"请填写完整信息"
                });
                return;
            }
            if(this.changepwdForm.newpwd!=this.changepwdForm.confirmpwd){
                this.$notify.error({
                    title: '提示',
                    message:"两次输入的密码不一致"
                });
                return;
            }
            //检查表单E
            this.$root.loading(true);
            let data = null;
            let postData = {
                code:this.changepwdForm.code,
                newpassword:this.changepwdForm.newpwd,
            }
            try{
                data = await axios.post(this.URL+"/user/changepwd",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                this.closeChangePwd();
                return;
            }
            this.closeChangePwd();
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '提示',
                message: data?.data?.msg,
            });
        },
        //调用邮箱验证码发送接口
        async sendEmailCode(event="",code=""){
            this.$root.loading(true);
            let data = null;
            let postData = {
                id:event,
                code:code
            }
            if(event=="changeemailS2"){
                postData.email = this.changeemailForm.email;
            }
            try{
                data = await axios.post(this.URL+"/user/sendMail",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }           
            this.$notify.error({
                title: '提示',
                message: data?.data?.msg,
            });
        },
        //获取用于修改密码的hcaptcha token
        getpwdToken(){
            this.isShowHcaptcha = true;
            this.hCaptchaType = 'changepwd';
        },
        //获取用于修改邮箱的hcaptcha token
        getemailToken(event=""){
            this.isShowHcaptcha = true;
            this.hCaptchaType = event;
        },
        //关闭hcaptcha
        closeHcaptcha(){
            this.isShowHcaptcha = false;     
        },
        //hcaptcha验证结果
        async hcapResult(token){
            if(token){
                this.$refs?.hcaptcha?.hide();
                this.sendEmailCode(this.hCaptchaType,token);
            }
        },
        sortChange(column){
            this.sort = column.prop;
            if(column.prop == "level_text"){
                this.sort = "level_id";
            }
            if(column.order == "descending"){
                this.order = "desc";
            }else{
                this.order = "asc";
            }
            this.getData();
        },
        formatTraffic(full=0, used=0) {
            if(!used){
                used = 0;
            }
            if(!full){
                full = 0;
            }
            let value = 0;
            if(full > used){
                value= Math.floor((full - used) / full * 100);
            }            
            return {
                str:value+"% ("+(full-used)+"MB/"+full+"MB)",
                value:value
            };
        },
        reloadData(){
            this.getData();
            this.getUserInfo();
        },
        changePage(page) {
            this.currentPage = page;
            this.getData(page);
        },
        async getData(page=1,limit=10){
            this.loading=true;
            let data = null;
            try{
                data = await axios.get(
                    this.URL + "buy/getUserCardList?page="+page+"&limit="+limit+"&code="+this.searchCode+"&status=&sort="+this.sort+"&order="+this.order,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.loading=false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading=false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: "提示",
                    message: data?.data?.msg,
                });
                return;
            }
            this.totalPage = data?.data?.data?.last_page ?? 1;
            this.cardsData = data?.data?.data?.data ?? [];
        },
        //获取用户信息
        async getUserInfo() {
            let res = await this.$root.checkLogin();
            if(!res){
                if(this.$route.path != "/"){
                this.$router.replace("/");
                }        
                this.$root.goLogin();
            }
            this.userInfo = res?.data ?? null;
        },
        //列表搜索防抖
        debouncedGetData: (function () {
            let timer = null;
            return function () {
                if (timer) clearTimeout(timer);
                timer = setTimeout(() => {
                    this.getData(1,this.limit);
                }, 500);
            }
        })(),
    },
    mounted() {
        this.$nextTick(() => {
            //获取用户信息
            this.getUserInfo();
            this.getData();
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/userPage.scss">

</style>