<template>
    <div id="homePage">
        <div class="logo">
            <img :src="logo"/>
            <span>千鹤FRP</span>
            <span class="copyright">千鹤FRP版权所有</span>
        </div>
        <div id="backgroundBanner" class="backgroundBanner">
            <div class="backGround h1BackGround" :style="'opacity:'+h1Opacity"></div>
            <div class="backGround h2BackGround" :style="'opacity:'+h2Opacity"></div>
            <div class="backGround h3BackGround" :style="'opacity:'+h3Opacity"></div>
            <div class="backGround h4BackGround" :style="'opacity:'+h4Opacity"></div>

            <div id="h1Content" class="mainContent h1Content">
                <!-- <div class="helpMsgTop">别拉啦...真的没有了...</div> -->
                <img class="title" :src="font"/>
                <div class="btnGroup">
                    <div class="btn" @click="goManage()">
                        <div class="text">管理面板</div>
                    </div>
                    <div class="btn" @click="$root.goReg()">
                        <div class="text">注册账号</div>
                    </div>
                </div>
            </div>

            <div id="h2Content" class="mainContent h2Content">
                <div class="title">介绍</div>
                <div class="textBox" @touchmove="$root.scrollLock=true" @touchend="$root.scrollLock=false">
                    <div class="text" v-html="h2Data.content??''"></div>
                </div>     
            </div>

            <div id="h3Content" class="mainContent h3Content">
                <div class="title">
                    <div class="text">定价</div>
                    <div class="more" @click="goMall()">更多套餐</div>
                </div>
                <div class="prices">
                    <div class="item" v-for="(item,key) in priceData" :key="key">   
                        <div class="name">{{ item.name }}</div>        
                        <div class="itemContent" v-html="item.content"></div>   
                        <div class="bottom">
                             <div class="price">价格:<span class="num">{{ item.price }}</span>/月</div>    
                             <div class="btn" @click="goMall()">
                                前去购买
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div id="h4Content" class="mainContent h4Content">
                <!-- <div class="helpMsgBottom">别拉啦...已经到底了...</div> -->
                <div class="title">团队</div>
                <div class="teamBox" @touchmove="$root.scrollLock=true" @touchend="$root.scrollLock=false">
                    <div class="person igron" v-for="(item,key) in h4Data" :key="'team_'+key">
                        <div class="name igron">
                            <img :src="item.img"/>
                            {{item.name}}
                        </div>
                        <div class="job igron">
                            职责:{{ item.job }}
                        </div>
                        <div class="intro igron" v-html="item.content"></div>
                    </div>
                </div>       
            </div>
        </div>
    </div>    
</template>

<script>
import font from "@/assets/img/font.png"
import logo from "@/assets/img/logo.png"
import axios from "axios"

export default {
  name: 'HomePage',
  components: {
  }, 
  data() {
    return {
        font:font,
        logo:logo,
        URL:window.config.apiUrl,
        priceData:[],
        h2Data:[],
        h4Data:[],
        pageList:["h1","h2","h3","h4"],
        currentPage:"h1",
        h1Opacity:1,
        h2Opacity:0,
        h3Opacity:0,
        h4Opacity:0,
        scrollListenerObject:null,
    }
  },
  watch:{
  },
  methods: {
    // 监听滚动事件
    scrollListener() {
        let box = document.querySelector("#backgroundBanner");
        if(!box) return;
        let listener = box.addEventListener("scroll", () => {
            let scrollTop = box.scrollTop;
            let clientHeight = box.clientHeight;
            // 计算滚动比例
            let ratio = scrollTop / clientHeight;
            // 各标题透明度
            this.h1Opacity = 1 - ratio;          // 第一屏
            this.h2Opacity = ratio;              // 第二屏
            this.h3Opacity = ratio - 1;          // 第三屏
            this.h4Opacity = ratio - 2;          // 第四屏
        }, { passive: false });
        this.scrollListenerObject = listener;
    },
    //请求套餐数据
    getPrice(page=1,limit=3){
        axios.get(this.URL+"content/package?page="+page+"&limit="+limit)
        .then(res=>{ 
            this.priceData = res.data.data.data ?? [];
        })
        .catch(err=>{ 
            this.$root.netError(err);
        });
    },
    //请求内容文本数据
    getData(){
        axios.get(this.URL+"content/front")
        .then(res=>{ 
            let data = res.data.data ?? [];
            this.h2Data = data.intro[0] ?? [];
            this.h4Data = data.team ?? [];
        })
        .catch(err=>{ 
            this.$root.netError(err);
        });
    },
    //前往商城页
    async goMall(){
        let isLogin = await this.$root.checkLogin();
        if(isLogin){
            if(this.$route.path != "/mall"){
                this.$router.replace("/mall");
            }
            return;
        }else{
            this.$root.goLogin();
        }
    },
    //前往管理面板
    async goManage(){
        let isLogin = await this.$root.checkLogin();
        if(isLogin){
            if(this.$route.path != "/usercenter"){
                this.$router.replace("/usercenter");
            }
            return;
        }else{
            this.$root.goLogin();
        }
    },
  },
  beforeCreate(){
  },
  created(){
    this.getPrice();
    this.getData();
  },
  mounted(){
    this.$nextTick(()=>{
        this.scrollListener();
    })
  },
  destroyed(){
    //清理监听器
    let box = document.querySelector("#backgroundBanner");
    if(box){
        box.removeEventListener("scroll", this.scrollListenerObject);
    }    
  }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/homePage.scss"></style>