<template>
    <div id="userCenterPage" class="page">
        <div class="mainContent">
            <el-tabs v-model="activeName">
              <el-tab-pane label="用户信息" name="userdata">
                <userData :userInfo="userInfo"/>
              </el-tab-pane>
              <el-tab-pane label="每日签到" name="signin">
                <signIn :userInfo="userInfo" @finish="getUserInfo"/>
              </el-tab-pane>
              <el-tab-pane label="流量统计" name="trafficchart">
                <chartBox ref="chartBox" name="trafficChart" title="流量统计"/>
              </el-tab-pane>
              <el-tab-pane label="公告列表" name="announces">
                <announceList :showMore="true"/>
              </el-tab-pane>
            </el-tabs>
        </div>
    </div>
</template>

<script> 
import userData from "@/components/lib/userData.vue";
import signIn from "@/components/lib/signIn.vue";
import announceList from "@/components/lib/announceList.vue";
import chartBox from "@/components/lib/chartBox.vue";


export default {
  name: 'userCenterPage',
  components: {
    userData,
    signIn,
    announceList,
    chartBox,
  },
  data() {
    return {
      activeName:"userdata",
      userInfo: null
    }
  },
  watch: {
    'activeName':function(v){
      if(v != "trafficchart"){return}
      this.$nextTick(() => {
        this.$refs?.chartBox?.createChart();
      });
    }
  },
  methods: {
    //获取用户信息
    async getUserInfo() {
      let res = await this.$root.checkLogin();
      if(!res){
        if(this.$route.path != "/"){
          this.$router.replace("/");
        }        
        this.$root.goLogin();
      }
      this.userInfo = res?.data ?? null;
    }
  },
  created() {
  },
  mounted() {
    this.$nextTick(() => {
      //获取用户信息
      this.getUserInfo();
    });
  },
}
</script>
<style lang="scss" scoped src="@/assets/css/view/userCenterPage.scss">

</style>